<template>
  <div class="search-view">
    <h2>Sermon Search</h2>
    
    <div class="search-box">
      <input 
        v-model="searchQuery" 
        @input="handleSearch"
        type="text" 
        placeholder="Search sermons by title or transcript content..."
        class="search-input"
        :disabled="!db"
      >
    </div>

    <div v-if="loading" class="loading">
      <div class="loading-spinner"></div>
      <p>Searching...</p>
    </div>

    <div v-else-if="results.length > 0" class="results">
      <div class="results-summary">
        <h3>Found {{ results.length }} matches</h3>
        <div class="stats">
          <span>Title Matches: {{ titleMatches }}</span>
          <span>Transcript Matches: {{ transcriptMatches }}</span>
        </div>
      </div>

      <div class="results-tabs">
        <button 
          :class="['result-tab', { active: resultTab === 'all' }]"
          @click="resultTab = 'all'"
        >
          All Results ({{ results.length }})
        </button>
        <button 
          :class="['result-tab', { active: resultTab === 'titles' }]"
          @click="resultTab = 'titles'"
        >
          Title Matches ({{ titleMatches }})
        </button>
        <button 
          :class="['result-tab', { active: resultTab === 'transcripts' }]"
          @click="resultTab = 'transcripts'"
        >
          Transcript Matches ({{ transcriptMatches }})
        </button>
      </div>

      <div class="results-table">
        <table>
          <thead>
            <tr>
              <th class="col-type" @click="sortBy('type')" style="cursor: pointer;">
                Type {{ sortColumn === 'type' ? (sortDirection === 'asc' ? '‚ñ≤' : '‚ñº') : '' }}
              </th>
              <th class="col-date" @click="sortBy('date')" style="cursor: pointer;">
                Date {{ sortColumn === 'date' ? (sortDirection === 'asc' ? '‚ñ≤' : '‚ñº') : '' }}
              </th>
              <th class="col-match">Match</th>
              <th class="col-title" @click="sortBy('title')" style="cursor: pointer;">
                Video Title {{ sortColumn === 'title' ? (sortDirection === 'asc' ? '‚ñ≤' : '‚ñº') : '' }}
              </th>
              <th class="col-time" @click="sortBy('timestamp')" style="cursor: pointer;">
                Time {{ sortColumn === 'timestamp' ? (sortDirection === 'asc' ? '‚ñ≤' : '‚ñº') : '' }}
              </th>
              <th class="col-speaker" @click="sortBy('speaker')" style="cursor: pointer;">
                Speaker {{ sortColumn === 'speaker' ? (sortDirection === 'asc' ? '‚ñ≤' : '‚ñº') : '' }}
              </th>
              <th class="col-watch">Watch</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(result, index) in filteredResults" :key="index">
              <td>
                <span :class="result.type === 'Title' ? 'badge-title' : 'badge-transcript'">
                  {{ result.type }}
                </span>
              </td>
              <td>{{ result.date }}</td>
              <td class="match-text">{{ result.match }}</td>
              <td class="title-cell">{{ result.title }}</td>
              <td>{{ result.timestamp }}</td>
              <td>{{ result.speaker }}</td>
              <td>
                <a :href="result.url" target="_blank" class="watch-link" title="Watch video">‚ñ∂</a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div v-else-if="searchQuery.length >= 2" class="no-results">
      No matches found for "{{ searchQuery }}"
    </div>

    <div v-else-if="db" class="instructions">
      <p>üîç Enter at least 2 characters to search</p>
      <p class="hint">Try searching for topics like "faith", "prayer", "salvation", or Bible references</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchView',
  props: {
    db: {
      type: Object,
      required: true
    },
    speakers: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      searchQuery: '',
      results: [],
      loading: false,
      resultTab: 'all',
      searchTimeout: null,
      sortColumn: 'date',
      sortDirection: 'desc'
    }
  },
  computed: {
    titleMatches() {
      return this.results.filter(r => r.type === 'Title').length
    },
    transcriptMatches() {
      return this.results.filter(r => r.type === 'Transcript').length
    },
    filteredResults() {
      let filtered = this.results
      
      if (this.resultTab === 'titles') {
        filtered = filtered.filter(r => r.type === 'Title')
      } else if (this.resultTab === 'transcripts') {
        filtered = filtered.filter(r => r.type === 'Transcript')
      }
      
      return this.sortResults(filtered)
    }
  },
  methods: {
    handleSearch() {
      if (this.searchQuery.length < 2) {
        this.results = []
        this.loading = false
        return
      }
      
      this.loading = true
      
      clearTimeout(this.searchTimeout)
      this.searchTimeout = setTimeout(() => {
        this.performSearch()
        this.loading = false
      }, 500)
    },
    
    performSearch() {
      if (!this.db) {
        console.error('Database not ready')
        return
      }
      
      console.log(`Searching for: "${this.searchQuery}"`)
      const results = []
      const query = this.searchQuery.trim()
      
      try {
        // Search video titles
        const titleQuery = `
          SELECT video_id, title, date_published, url
          FROM videos
          WHERE title LIKE ?
          ORDER BY date_published DESC
        `
        
        const titleResults = this.db.exec(titleQuery, [`%${query}%`])
        
        if (titleResults.length > 0) {
          const rows = titleResults[0].values
          rows.forEach(row => {
            const [videoId, title, date, url] = row
            results.push({
              type: 'Title',
              date: date.substring(0, 10),
              speaker: this.speakers[videoId] || 'Unknown',
              title: title,
              timestamp: '00:00:00',
              match: `Title contains: "${query}"`,
              url: url,
              score: 10
            })
          })
        }
        
        console.log(`Found ${results.length} title matches`)
        
        // Search transcripts - NO LIMIT for all results
        const transcriptQuery = `
          SELECT ts.video_id, v.title, v.date_published, ts.start_time, ts.text, ts.vimeo_url
          FROM transcript_segments ts
          JOIN videos v ON ts.video_id = v.video_id
          WHERE ts.text LIKE ?
          ORDER BY v.date_published DESC, ts.start_time
        `
        
        console.log('Searching transcripts...')
        const startTime = performance.now()
        const transcriptResults = this.db.exec(transcriptQuery, [`%${query}%`])
        const searchTime = ((performance.now() - startTime) / 1000).toFixed(2)
        
        if (transcriptResults.length > 0) {
          const rows = transcriptResults[0].values
          console.log(`Found ${rows.length} transcript matches in ${searchTime}s`)
          
          rows.forEach(row => {
            const [videoId, title, date, startTime, text, url] = row
            results.push({
              type: 'Transcript',
              date: date.substring(0, 10),
              speaker: this.speakers[videoId] || 'Unknown',
              title: title,
              timestamp: this.formatTimestamp(startTime),
              match: text.substring(0, 200) + (text.length > 200 ? '...' : ''),
              url: url,
              score: 1
            })
          })
        }
        
        console.log(`Total results: ${results.length}`)
        this.results = results
        
      } catch (error) {
        console.error('Search error:', error)
        alert(`Search failed: ${error.message}`)
      }
    },
    
    sortBy(column) {
      if (this.sortColumn === column) {
        this.sortDirection = this.sortDirection === 'asc' ? 'desc' : 'asc'
      } else {
        this.sortColumn = column
        this.sortDirection = 'desc'
      }
    },
    
    sortResults(results) {
      const sorted = [...results]
      
      sorted.sort((a, b) => {
        // Prioritize Transcript over Title
        if (a.type !== b.type) {
          return a.type === 'Transcript' ? -1 : 1
        }
        
        let aVal = a[this.sortColumn]
        let bVal = b[this.sortColumn]
        
        if (this.sortColumn === 'date') {
          aVal = new Date(aVal)
          bVal = new Date(bVal)
        } else if (this.sortColumn === 'timestamp') {
          aVal = this.timestampToSeconds(aVal)
          bVal = this.timestampToSeconds(bVal)
        }
        
        if (aVal < bVal) return this.sortDirection === 'asc' ? -1 : 1
        if (aVal > bVal) return this.sortDirection === 'asc' ? 1 : -1
        return 0
      })
      
      return sorted
    },
    
    timestampToSeconds(timestamp) {
      const parts = timestamp.split(':')
      return parseInt(parts[0]) * 3600 + parseInt(parts[1]) * 60 + parseInt(parts[2])
    },
    
    formatTimestamp(seconds) {
      const hours = Math.floor(seconds / 3600)
      const minutes = Math.floor((seconds % 3600) / 60)
      const secs = Math.floor(seconds % 60)
      return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
    }
  }
}
</script>

<style scoped>
.search-view {
  max-width: 100%;
  padding: 0 1rem;
}

h2 {
  margin-bottom: 1.5rem;
  color: #31333F;
  font-size: 2rem;
  font-weight: 600;
}

.search-box {
  margin-bottom: 1.5rem;
}

.search-input {
  width: 100%;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  font-family: "Source Sans Pro", sans-serif;
  border: 1px solid #d3d8e0;
  border-radius: 0.25rem;
  transition: border-color 0.2s;
  background-color: #ffffff;
}

.search-input:focus {
  outline: none;
  border-color: #ff2b2b;
  box-shadow: 0 0 0 1px #ff2b2b;
}

.search-input:disabled {
  background: #f0f2f6;
  cursor: not-allowed;
}

.loading {
  text-align: center;
  padding: 40px 20px;
  color: #31333F;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  margin: 0 auto 15px;
  border: 4px solid #f0f2f6;
  border-top: 4px solid #ff2b2b;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading p {
  font-size: 1rem;
  margin: 10px 0;
}

.results-summary {
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
  padding: 20px;
  border-radius: 0.25rem;
  margin-bottom: 1.5rem;
  border-left: 4px solid #28a745;
}

.results-summary h3 {
  color: #155724;
  margin-bottom: 10px;
  font-size: 1.1rem;
  font-weight: 600;
}

.stats {
  display: flex;
  gap: 30px;
  color: #155724;
  font-size: 0.95rem;
}

.stats span {
  padding: 5px 10px;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 4px;
}

.results-tabs {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.result-tab {
  padding: 0.5rem 1rem;
  background: #f0f2f6;
  border: 1px solid #d3d8e0;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 0.95rem;
  font-family: "Source Sans Pro", sans-serif;
  color: #31333F;
}

.result-tab:hover {
  background: #e6e9ef;
  border-color: #cbd5e0;
}

.result-tab.active {
  background: #0068c9;
  color: white;
  border-color: #0068c9;
  font-weight: 500;
}

.results-table {
  overflow-x: auto;
  background: white;
  border: 1px solid #e6e9ef;
  border-radius: 0.25rem;
}

table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.95rem;
}

thead {
  background: #f0f2f6;
  position: sticky;
  top: 0;
  z-index: 10;
}

th {
  padding: 0.75rem;
  text-align: left;
  font-weight: 600;
  color: #31333F;
  border-bottom: 2px solid #e6e9ef;
  white-space: nowrap;
  font-size: 0.875rem;
}

th.col-type { width: 80px; }
th.col-date { width: 100px; }
th.col-match { width: 35%; }
th.col-title { width: 30%; }
th.col-time { width: 80px; }
th.col-speaker { width: 120px; }
th.col-watch { width: 60px; }

td {
  padding: 0.75rem;
  border-bottom: 1px solid #f0f2f6;
  vertical-align: top;
  color: #31333F;
}

tr:hover {
  background: #fafbfc;
}

.badge-title {
  background: #0068c9;
  color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  font-size: 0.8rem;
  font-weight: 600;
}

.badge-transcript {
  background: #28a745;
  color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  font-size: 0.8rem;
  font-weight: 600;
}

.title-cell {
  font-weight: 500;
  color: #31333F;
}

.match-text {
  line-height: 1.5;
  color: #555;
  overflow-wrap: break-word;
}

.watch-link {
  color: #0068c9;
  text-decoration: none;
  font-size: 1.2rem;
  transition: all 0.2s;
  display: inline-block;
}

.watch-link:hover {
  color: #ff2b2b;
  transform: scale(1.15);
}

.no-results {
  text-align: center;
  padding: 60px 20px;
  color: #31333F;
  font-size: 1rem;
}

.no-results::before {
  content: 'üîç';
  display: block;
  font-size: 3rem;
  margin-bottom: 20px;
  opacity: 0.5;
}

.instructions {
  text-align: center;
  padding: 60px 20px;
  color: #31333F;
}

.instructions p {
  margin: 10px 0;
  font-size: 1.05rem;
}

.instructions .hint {
  color: #808495;
  font-size: 0.95rem;
  font-style: normal;
}

@media (max-width: 768px) {
  .search-view {
    padding: 0 0.5rem;
  }
  
  .results-table {
    font-size: 0.85rem;
  }
  
  th, td {
    padding: 0.5rem;
  }
  
  .match-text {
    max-width: 200px;
  }
  
  .stats {
    flex-direction: column;
    gap: 0.5rem;
  }
}
</style>
