<template>
  <div id="app">
    <header>
      <img src="./assets/download.png" alt="HHBC Sermon Search" class="logo" v-if="logoExists">
      <h1 v-else>HHBC Sermon Search</h1>
    </header>

    <!-- Show loading overlay if database is loading -->
    <div v-if="loadingDb" class="global-loading">
      <div class="loading-spinner"></div>
      <p>{{ loadingProgress }}</p>
      <p class="loading-hint">This may take a minute on first load (142MB compressed)</p>
      <p v-if="loadingProgress.includes('Error')" class="error-hint">
        If you're on a mobile device, the database may be too large. Please try on a desktop computer.
      </p>
    </div>

    <div v-else>
      <nav class="tabs">
        <button 
          v-for="tab in tabs" 
          :key="tab.id"
          :class="['tab', { active: currentTab === tab.id }]"
          @click="currentTab = tab.id"
        >
          {{ tab.name }}
        </button>
      </nav>

      <main>
        <SearchView v-if="currentTab === 'search'" :db="db" :speakers="speakers" />
        <VideoListView v-if="currentTab === 'videos'" :db="db" :speakers="speakers" />
        <BibleHeatMapView v-if="currentTab === 'bible'" :db="db" :speakers="speakers" />
      </main>
    </div>
  </div>
</template>

<script>
import SearchView from './views/SearchView.vue'
import VideoListView from './views/VideoListView.vue'
import BibleHeatMapView from './views/BibleHeatMapView.vue'
import initSqlJs from 'sql.js' // eslint-disable-line
import pako from 'pako' // eslint-disable-line

export default {
  name: 'App',
  components: {
    SearchView,
    VideoListView,
    BibleHeatMapView
  },
  data() {
    return {
      currentTab: 'search',
      logoExists: false,
      tabs: [
        { id: 'search', name: 'Home' },
        { id: 'videos', name: 'Video List' },
        { id: 'bible', name: 'Bible Heat Map' }
      ],
      loadingDb: true,
      loadingProgress: '',
      db: null,
      speakers: {}
    }
  },
  async mounted() {
    this.checkLogo()
    await this.loadDatabase()
  },
  beforeUnmount() {
    if (this.db) {
      this.db.close()
    }
  },
  methods: {
    checkLogo() {
      const img = new Image()
      img.onload = () => { this.logoExists = true }
      img.onerror = () => { this.logoExists = false }
      img.src = require('./assets/download.png')
    },
    
    async loadDatabase() {
      try {
        const basePath = process.env.NODE_ENV === 'production' ? '/hhbc-sermon-search/' : '/'
        
        // STEP 1: Load Database (0-60%)
        this.loadingProgress = 'Step 1 of 3: Loading database... 0%'
        
        const SQL = await initSqlJs({
          locateFile: file => `https://sql.js.org/dist/${file}`
        })
        
        this.loadingProgress = 'Step 1 of 3: Loading database... 5%'
        
        const chunk0Response = await fetch(`${basePath}data/transcripts.db.gz.part0`)
        if (!chunk0Response.ok) {
          throw new Error(`Chunk 0 failed: ${chunk0Response.status}`)
        }
        
        const chunk0Total = parseInt(chunk0Response.headers.get('content-length'))
        let chunk0Loaded = 0
        const chunk0Reader = chunk0Response.body.getReader()
        const chunk0Chunks = []
        
        let done = false
        while (!done) {
          const result = await chunk0Reader.read()
          done = result.done
          
          if (result.value) {
            chunk0Chunks.push(result.value)
            chunk0Loaded += result.value.length
            const percent = 5 + Math.round((chunk0Loaded / chunk0Total) * 25)
            this.loadingProgress = `Step 1 of 3: Loading database... ${percent}%`
          }
        }
        
        const chunk0Array = new Uint8Array(chunk0Loaded)
        let position = 0
        for (const chunk of chunk0Chunks) {
          chunk0Array.set(chunk, position)
          position += chunk.length
        }
        
        this.loadingProgress = 'Step 1 of 3: Loading database... 30%'
        
        const chunk1Response = await fetch(`${basePath}data/transcripts.db.gz.part1`)
        if (!chunk1Response.ok) {
          throw new Error(`Chunk 1 failed: ${chunk1Response.status}`)
        }
        
        const chunk1Total = parseInt(chunk1Response.headers.get('content-length'))
        let chunk1Loaded = 0
        const chunk1Reader = chunk1Response.body.getReader()
        const chunk1Chunks = []
        
        done = false
        while (!done) {
          const result = await chunk1Reader.read()
          done = result.done
          
          if (result.value) {
            chunk1Chunks.push(result.value)
            chunk1Loaded += result.value.length
            const percent = 30 + Math.round((chunk1Loaded / chunk1Total) * 25)
            this.loadingProgress = `Step 1 of 3: Loading database... ${percent}%`
          }
        }
        
        const chunk1Array = new Uint8Array(chunk1Loaded)
        position = 0
        for (const chunk of chunk1Chunks) {
          chunk1Array.set(chunk, position)
          position += chunk.length
        }
        
        this.loadingProgress = 'Step 1 of 3: Loading database... 55%'
        
        // Combine chunks
        const combined = new Uint8Array(chunk0Array.length + chunk1Array.length)
        combined.set(chunk0Array, 0)
        combined.set(chunk1Array, chunk0Array.length)
        
        // Clear to free memory
        chunk0Chunks.length = 0
        chunk1Chunks.length = 0
        
        this.loadingProgress = 'Step 1 of 3: Loading database... 60%'
        
        // STEP 2: Decompress
        this.loadingProgress = 'Step 2 of 3: Decompressing database...'
        await new Promise(resolve => setTimeout(resolve, 100))
        
        let decompressedData
        try {
          decompressedData = pako.ungzip(combined)
        } catch (decompError) {
          throw new Error(`Decompression failed: ${decompError.message}`)
        }
        
        this.loadingProgress = 'Step 2 of 3: Decompressing complete... 80%'
        
        // STEP 3: Initialize
        this.loadingProgress = 'Step 3 of 3: Initializing search engine... 85%'
        await new Promise(resolve => setTimeout(resolve, 100))
        
        try {
          this.db = new SQL.Database(decompressedData)
        } catch (sqlError) {
          throw new Error(`SQL initialization failed: ${sqlError.message}. Your device may not have enough memory for the ${(decompressedData.length / 1024 / 1024).toFixed(0)} MB database.`)
        }
        
        this.loadingProgress = 'Step 3 of 3: Initializing search engine... 95%'
        await this.loadSpeakers()
        
        this.loadingProgress = 'Step 3 of 3: Complete! 100%'
        await new Promise(resolve => setTimeout(resolve, 300))
        
        this.loadingDb = false
        
      } catch (error) {
        console.error('Error loading database:', error)
        this.loadingProgress = `‚ùå Error: ${error.message}`
        
        // Don't use alert - show error on page instead
        // User can see the error message in loadingProgress
      }
    },
    
    async loadSpeakers() {
      try {
        const basePath = process.env.NODE_ENV === 'production' ? '/hhbc-sermon-search/' : '/'
        const response = await fetch(`${basePath}data/speakers.json`)
        if (response.ok) {
          this.speakers = await response.json()
          console.log(`‚úÖ Loaded ${Object.keys(this.speakers).length} speaker mappings`)
        }
      } catch (error) {
        console.warn('Could not load speakers.json:', error)
      }
    }
  }
}
</script>

